import React, { useState } from "react";
import "./modal.css";

import { useDispatch, useSelector } from "react-redux";
import PaginationLimit from "../Pagination/PaginationLimit";
import PaginationBlock from "../PaginationBlock";
import {
  getNext,
  getParticularPage,
  getPrevious,
} from "../../Redux/PrintReport/thunk";

const PrintsReportModal = ({
  showPrintReportModal,
  setShowPrintReportModal,
  formData,
  prints,
}) => {
  const { district, branch, user, formatedStartDate, formatedEndDate } =
    formData;
  const loading = useSelector((state) => state.printReport.loading);
  const count = useSelector((state) => state.printReport.count);
  const previous = useSelector((state) => state.printReport.previous);
  const next = useSelector((state) => state.printReport.next);
  const dispatch = useDispatch();
  const modalClass = showPrintReportModal
    ? "modal display-block"
    : "modal display-none";

  const [currentPage, setCurrentPage] = useState(1);
  const [maxPageNumberLimit, setMaxPageNumberLimit] = useState(5);
  const [minPageNumberLimit, setMinPageNumberLimit] = useState(0);
  const [postsPerPage, setPostsPerPage] = useState(10);
  const pageNumberLimit = 5;
  //change page
  const paginate = (number) => {
    dispatch(
      getParticularPage({
        number,
        postsPerPage,
        district,
        branch,
        user,
        formatedStartDate,
        formatedEndDate,
      })
    );
    setCurrentPage(number);
  };

  //handle Click
  const handleClick = (type) => {
    if (type === "previous") {
      dispatch(getPrevious(previous));
      setCurrentPage((prevState) => prevState - 1);
      if ((currentPage - 1) % pageNumberLimit === 0) {
        setMaxPageNumberLimit(maxPageNumberLimit - pageNumberLimit);
        setMinPageNumberLimit(minPageNumberLimit - pageNumberLimit);
      }
    } else if (type === "next") {
      dispatch(getNext(next));
      setCurrentPage((prevState) => prevState + 1);
      if (currentPage + 1 > maxPageNumberLimit) {
        setMaxPageNumberLimit(maxPageNumberLimit + pageNumberLimit);
        setMinPageNumberLimit(minPageNumberLimit + pageNumberLimit);
      }
    }
  };

  return (
    <>
      <div className={modalClass} tabIndex="-1">
        <div className="container-fluid main-container">
          <div className="modal-content content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Card Print Report
              </h5>
              <button
                type="button"
                className="btn-close"
                data-dismiss="modal"
                aria-label="Close"
                onClick={() => setShowPrintReportModal(false)}
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              {prints?.length > 0 && (
                <>
                  <div className="col-6">
                    <PaginationLimit
                      postsPerPage={postsPerPage}
                      setPostsPerPage={setPostsPerPage}
                    />
                  </div>
                </>
              )}
              {loading ? (
                <div className="d-flex justify-content-center ">
                  <div className="spinner-border" role="status">
                    <span className="sr-only">Loading...</span>
                  </div>
                </div>
              ) : (
                <>
                  {prints?.length === 0 ? (
                    "You have No records"
                  ) : (
                    <table className="table table table-bordered ">
                      <thead className="text-center">
                        <tr className="text-center">
                          <th scope="col">#</th>
                          <th scope="col">Card No</th>
                          <th scope="col">Name </th>
                          <th scope="col">Address</th>
                          <th scope="col">Mobile Number</th>
                          <th scope="col">Branch</th>
                        </tr>
                      </thead>
                      <tbody>
                        {prints?.length > 0 &&
                          prints.map((detail, i) => {
                            const {
                              id,
                              card_no,
                              first_name_nepali,
                              middle_name_nepali,
                              last_name_nepali,
                              address_nepali,
                              mobile_no_nepali,
                              branch,
                            } = detail;
                            return (
                              <tr key={id} className="text-center">
                                <td>
                                  {" "}
                                  {postsPerPage * (currentPage - 1) + (i + 1)}
                                </td>
                                <td>{card_no}</td>

                                <td>
                                  {first_name_nepali +
                                    " " +
                                    middle_name_nepali +
                                    " " +
                                    last_name_nepali}
                                </td>
                                <td>{address_nepali}</td>
                                <td>{mobile_no_nepali}</td>
                                <td>{branch.name}</td>
                              </tr>
                            );
                          })}
                      </tbody>
                    </table>
                  )}
                </>
              )}
            </div>
            {count > 0 && (
              <PaginationBlock
                currentPage={currentPage}
                postsPerPage={postsPerPage}
                count={count}
                paginate={paginate}
                handleClick={handleClick}
                setCurrentPage={setCurrentPage}
                minPageNumberLimit={minPageNumberLimit}
                maxPageNumberLimit={maxPageNumberLimit}
              />
            )}
          </div>
        </div>{" "}
      </div>
    </>
  );
};
export default React.memo(PrintsReportModal);
